@import "sass/index";

.container {
  position: relative;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    84.48deg,
    #25013f 1.13%,
    #4b0082 27.68%,
    #9400d3 67%,
    #210038 99.17%
  );

  background-size: cover;
  border-radius: 30px;
  overflow: hidden;

  font-family: "Gilroy";

  @media (max-width: 600px) {
    border-radius: 10px;
  }
}

.bg {
  width: 100%;
  height: 100%;
}

.disable {
  & * {
    animation: none !important;
  }
}
