@import "sass/index";

.wrapper {
  position: relative;
  width: 100%;

  @media (max-width: $mobileScreen) {
    padding: 0 22px;
  }
}

.button {
  background: black !important;
  color: white;
  width: 32px;
  height: 32px;

  border-radius: 50%;
  box-shadow: none;
  padding: 0;

  opacity: 0.5;

  position: absolute;
  z-index: 1;
  top: calc(50% - 16px);
  left: 5px;
  @media (max-width: $mobileScreen) {
    left: 27px;
  }
  &__right {
    left: auto;
    right: 5px;
    @media (max-width: $mobileScreen) {
      right: 27px;
    }
  }
}

.dotsContainer {
  z-index: 1;
  position: absolute;
  bottom: 10px;
  left: 50%;
  margin: 10px;
  transform: translateX(-50%);
  transition: all 0.7s;
  box-shadow: none;
}
