@import "sass/index";

@keyframes buttonImg {
  0% {
    transform: scale(0.9);
  }

  100% {
    transform: scale(1.1);
  }
}

@keyframes leftWingAnimate {
  0% {
    transform: rotate(-2deg);
  }

  100% {
    transform: rotate(3deg);
  }
}

@keyframes rightWingAnimate {
  0% {
    transform: rotate(2deg);
  }

  100% {
    transform: rotate(-3deg);
  }
}

.mainTitle {
  position: absolute;
  left: 0;
  top: 0;
  pointer-events: none;
  height: 100% !important;
  width: auto !important;
}

.secondTitle {
  margin: 0;

  font-weight: 900;
  font-size: 2vw;
  color: #ffffff;
  text-align: left;
  line-height: 1;

  position: absolute;
  top: 43%;
  left: 2.6%;

  @media (min-width: 1400px) {
    font-size: 20px;
  }
}

.buttonWrapper {
  position: absolute;
  left: 6%;
  top: 62%;
  width: 26%;
  height: 18%;
}
.button {
  box-shadow: inset 0px -3px 0px 0px #d4ae73;
  background: #f3daa4 !important;
  border-radius: 1vw;

  padding: 0;
  height: 100%;
  width: 100%;
}

.buttonText {
  width: auto;
  height: 33%;
  max-height: 25px;
  position: absolute;
  animation: buttonImg 2s steps(30, end) infinite alternate;
}

.light {
  position: absolute;
  bottom: -62%;
  left: 21%;

  width: 55% !important;
  height: auto !important;
}

.wing {
  width: 75% !important;
  height: auto !important;

  position: absolute;
  bottom: -115%;
}
.rightWing {
  right: -23%;
  transform-origin: 0% 100%;

  animation: rightWingAnimate 2.5s steps(35, end) infinite alternate;
}
.leftWing {
  left: -23%;
  transform-origin: 100% 80%;

  animation: leftWingAnimate 2.5s steps(35, end) infinite alternate;
}
