@import "sass/index";

@keyframes rootRotate {
  0% {
    transform: rotate(-5deg);
  }

  100% {
    transform: rotate(5deg);
  }
}

@keyframes lightsRotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes cupScale {
  0% {
    transform: scale(0.9);
  }

  100% {
    transform: scale(1.05);
  }
}

.wrapper {
  position: absolute;
  bottom: -28%;
  right: 0%;
  width: 38%;

  animation: rootRotate 5s steps(65, end) infinite alternate;
}

.root {
  width: 100%;
}

.lights {
  position: absolute;
  right: 0%;
  bottom: 10%;
  width: 85%;
  z-index: 1;

  animation: lightsRotate 13s steps(190, end) infinite;
  opacity: 0.5;
}

.cup {
  position: absolute;
  left: 41%;
  top: 41%;
  width: 30% !important;
  z-index: 2;

  animation: cupScale 5s steps(35, end) infinite alternate;
}

@keyframes ballScale {
  0% {
    transform: scale(0);
    opacity: 0.1;
  }

  100% {
    transform: scale(1.05);
    opacity: 1;
  }
}

.ball {
  position: absolute;

  &__1 {
    top: 17%;
    left: 48%;
    width: 5% !important;

    animation: ballScale 5s steps(28, end) infinite alternate;
  }
  &__2 {
    left: 35%;
    top: 34%;
    width: 4% !important;
    animation: ballScale 3s steps(28, end) infinite alternate;
  }
  &__3 {
    left: 56%;
    top: 10%;
    width: 7% !important;
    animation: ballScale 2.5s steps(28, end) infinite alternate;
  }
  &__4 {
    left: 23%;
    top: 36%;
    width: 3% !important;
    animation: ballScale 4.5s steps(28, end) infinite alternate;
  }
  &__5 {
    top: 5%;
    left: 49%;
    width: 5% !important;
    animation: ballScale 5.5s steps(28, end) infinite alternate;
  }
  &__6 {
    width: 4% !important;
    animation: ballScale 6s steps(28, end) infinite alternate;

    left: 30%;
    top: 22%;
  }
  &__7 {
    width: 3% !important;
    animation: ballScale 3.5s steps(28, end) infinite alternate;

    top: 27%;
    left: 41%;
  }
}
