@import "sass/index";

@keyframes rotate_1 {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotate_2 {
  0% {
    transform: rotate(-6deg);
  }

  100% {
    transform: rotate(15deg);
  }
}

@keyframes rotate_3 {
  0% {
    transform: rotate(0deg) scale(0.8);
  }

  100% {
    transform: rotate(360deg) scale(1);
  }
}

.littleReward_01 {
  width: 15% !important;

  position: absolute;
  bottom: -20%;
  left: 35%;

  animation: rotate_1 10s steps(170, end) infinite;
}

.mediumReward_01 {
  width: 26% !important;

  position: absolute;
  bottom: -29%;
  right: 30%;

  animation: rotate_2 5s steps(55, end) infinite alternate;
}

.littleReward_02 {
  width: 9% !important;

  position: absolute;
  bottom: 82%;
  left: 65%;

  opacity: 0.8;

  animation: rotate_3 25s steps(270, end) infinite alternate;
}
